<template>
  <div id="app">
    <div id="filter">
      <div class="container" style="width:40%;">
        <div class="columns">
          <div class="column">
            <button class="filter-button" v-bind:class="{ active: this.isActive[0] }" @click="filterByCatId(14244, 0)"></button>
            <span class="filter-category">Informação</span>
          </div>
          <div class="column">
            <button class="filter-button" v-bind:class="{ active: this.isActive[1] }" @click="filterByCatId(14245, 1)"></button>
            <span class="filter-category">Inspiração</span>
          </div>
          <div class="column">
            <button class="filter-button" v-bind:class="{ active: this.isActive[2] }" @click="filterByCatId(14242, 2)"></button>
            <span class="filter-category">Reflexão</span>
          </div>
          <div class="column">
            <button class="filter-button" v-bind:class="{ active: this.isActive[3] }" @click="filterByCatId(14243, 3)"></button>
            <span class="filter-category">Distracção</span>
          </div>
        </div>
      </div>
    </div>
    <div id="filter-content" class="container" v-if="this.loaded">
      <transition-group tag="div" name="fade" class="columns is-multiline">
        <div class="column is-4 post" v-for="post in filterCat" :key="post.id">
            <div class="post-card">
            <div class="post-thumbnail">
              <a :href="post.link">
                <figure class="image is-2by1">
                  <img :src="post._embedded['wp:featuredmedia'][0].media_details.sizes.presso_thumbnail_medium.source_url">
                </figure>
              </a>
            </div>
            <div class="post-tags">
              <a class="post-tag"
              :href="tag.link"
              v-for="tag in post._embedded['wp:term'][0]"
              :key="tag.id"
              v-html="tag.name"
              v-if="!isBehavioral(tag.id)"></a>
            </div>
            <a class="post-title" v-html="post.title.rendered" :href="post.link"></a>
            <span class="post-meta">
              <span class="post-date">17 MAIO, 2018</span>
              <span class="post-author">João Ribeiro</span>
            </span>
          </div>
        </div>
      </transition-group>
      <div class="container" style="margin-top:50px" v-if="!this.loading">
        <button class="post-tag" id="load-more" @click="loadMore()">Carregar mais</button>
      </div>
      <div class="container" style="margin-top:50px" v-else>
        <div class="spinner">
          <div class="bounce1"></div>
          <div class="bounce2"></div>
          <div class="bounce3"></div>
        </div>
      </div>
    </div>
    <div class="container" v-else>
      <div class="spinner">
        <div class="bounce1"></div>
        <div class="bounce2"></div>
        <div class="bounce3"></div>
      </div>
    </div>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'
import Vue from 'vue'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data () {
    return {
      loaded: false,
      loading: false,
      posts: [],
      behavioral_categories: [14242, 14243, 14244, 14245],
      active_posts: [],
      page: 1,
      isActive: [false, false, false, false],
      active_categories: [],
      num_active_categories: 0
    }
  },
  created () {
    fetch(`https://shifter.pt/wp-json/wp/v2/posts?_embed&categories=14242+14243+14244+14245&categories_exclude=14239&page=${this.page}&per_page=12`).then(response => response.json()).then(json => {
      this.posts = json
    }).then(() => {
      this.posts = this.posts.slice(3)
      this.loaded = true
    })
  },
  methods: {
    loadMore () {
      this.loading = true
      this.page++
      fetch(`https://shifter.pt/wp-json/wp/v2/posts?_embed&categories=14242+14243+14244+14245&categories_exclude=14239&page=${this.page}&per_page=39`).then(response => response.json()).then(json => {
        this.posts = this.posts.concat(json)
      }).then(() => {
        this.loading = false
      })
    },
    isBehavioral (id) {
      return this.behavioral_categories.includes(id)
    },
    filterByCatId (id, cat) {
      if (!this.isActive[cat]) {
        Vue.set(this.isActive, cat, true)
        this.active_categories.push(id)
        this.num_active_categories++
        if (this.num_active_categories === 4) {
          this.isActive = [false, false, false, false]
          this.num_active_categories = 0
          this.active_categories = []
        }
      } else {
        Vue.set(this.isActive, cat, false)
        this.num_active_categories--
        if (this.active_categories.length === 1) {
          this.active_categories = []
          return
        }
        let index = this.active_categories.indexOf(id)
        if (index > -1) {
          this.active_categories.splice(index, 1)
        }
      }
    }
  },
  computed: {
    filterCat: function () {
      if (this.num_active_categories === 0) return this.posts

      let self = this
      return self.posts.filter(function (post) {
        return self.active_categories.some(function (category) {
          return post.categories.includes(category)
        })
      })
    }
  }
}
</script>
<style>
@import '~bulma/bulma'
body {
  padding:0;
  margin:0;
}
#filter {
  width:100%;
  background: #ffec39;
  padding:5px 0;
}
#filter-container {
  width:970px;
  max-width: 1040px;
}
#filter .filter-button {
  width:1rem;
  height:1rem;
  background: none;
  border: 1px solid #000;
  margin-right: 0.3rem;
  cursor: pointer;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
  vertical-align: middle;
}
#filter .filter-button:hover {
  background-color: #000;
}
#filter .filter-button.active {
   background-color: #000;
}
#filter .filter-category {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 12px;
  font-weight: bold;
  vertical-align: middle;
}

#filter-content {
  margin-top:2rem;
  letter-spacing: 0;
}
.post {
  transition: all 1s;
}
.post-card {
  color: #2c2c2c;
  text-align: left;
}
.post-tags {
  margin-top: 20px;
  margin-bottom:8px;
}
.post-tag {
  font-size: 12px;
  color: #2c2c2c;
  font-weight: bold;
  padding: 0px 5px;
  background-color: #ffec39;
  -webkit-transition: background-color 0.2s, color 0.2s;
  transition: background-color 0.2s, color 0.2s;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-right:2px;
}
.post-tag:hover {
  color: #ffec39;
  background-color: #2c2c2c;
}
.post-title {
  font-weight: 700;
  line-height: 1.2em;
  font-size:17px;
  color: #2c2c2c;
  letter-spacing: 0;
  display: inline-block;
}
.post-date {
  color: #2c2c2c;
  font-size:12px;
  margin-right: 0.4rem;
}
.post-author {
    text-transform: capitalize;
    font-weight: 900;
    color: #2c2c2c;
    font-size: 14px;
}
#load-more {
  padding-top: 5px;
  padding-bottom:5px;
  border:none;
  cursor: pointer;
}
.spinner {
  margin: 100px auto 0;
  width: 70px;
  text-align: center;
}
.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #ffec39;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}
.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% { -webkit-transform: scale(0) }
  40% { -webkit-transform: scale(1.0) }
}
@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}
.fade-leave-active {
  transition: opacity .5s;
  position: absolute;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateY(30px);
}
.fade-move {
  transition: transform 1s;
}
#app {
  font-family: 'Libre Franklin', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 60px 0;
}

</style>



